"use client";
import React from "react";
import Link from "next/link";
import LocationIcon from "../assets/icons/location_icon";
import PhoneIcon from "../assets/icons/phone_icon";
import DgNavLogo from "../assets/icons/dg_nav_logo";
import HeartIcon from "../assets/icons/heart_icon";
import CartIcon from "../assets/icons/cart_icon";
import BillIcon from "../assets/icons/bill_icon";
import UserIcon from "../assets/icons/user_icon";
import SearchIcon from "../assets/icons/search_icon";
import { useProducts } from "../store/productsStore.jsx";
import { toIntegerVND } from "../app/(user)/ring/utils/price";

const Nav = () => {
  const { products, status, fetchProducts } = useProducts();

  // Load products once so search is available globally
  React.useEffect(() => {
    if (status === "idle") fetchProducts();
  }, [status, fetchProducts]);

  const [query, setQuery] = React.useState("");
  const [debounced, setDebounced] = React.useState("");
  const [isopen, setIsOpen] = React.useState(false);

  // Debounce 1s after typing stops
  React.useEffect(() => {
    const t = setTimeout(() => setDebounced(query.trim()), 1000);
    return () => clearTimeout(t);
  }, [query]);

  const results = React.useMemo(() => {
    const q = debounced.toLowerCase();
    if (!q) return [];
    const list = Array.isArray(products) ? products : [];
    return list
      .filter((p) => String(p?.name || "").toLowerCase().includes(q))
      .slice(0, 8);
  }, [products, debounced]);

  const formatPrice = (p) => {
    const v = toIntegerVND(p);
    try {
      return new Intl.NumberFormat("vi-VN").format(v) + " ₫";
    } catch {
      return `${v} ₫`;
    }
  };

  return (
    <div className="flex flex-col">
      {/* make color */}
      <div className="h-[30px] bg-[#9B8D6F]"></div>
      {/* about */}
      <div className=" h-[100px] flex flex-row items-center justify-between mx-[65px]">
        <div className="flex flex-row items-center justify-center gap-[15px]">
          <a href="#">
            <LocationIcon />
          </a>
          <a href="#">
            <PhoneIcon />
          </a>
          <p>028 7939 3939</p>
        </div>
        <div>
          <Link href={"/home"}>
            <DgNavLogo />
          </Link>
        </div>
        <div className="flex flex-row items-center justify-center gap-[70px]">
          <div className="flex flex-row items-center justify-center gap-[18px]">
            <a href="#">
              <HeartIcon />
            </a>
            <a href="#">
              <CartIcon />
            </a>
            <a href="#">
              <BillIcon />
            </a>
            <a href="#">
              <UserIcon />
            </a>
          </div>
          <div className="h-[20px] flex flex-row justify-center gap-[24px]">
            <div>
              <a href="#">Về chúng tôi</a>
            </div>
            <div className=" flex flex-row items-center gap-[7px] leading-[20px]">
              <button className="cursor-pointer">EN</button>
              <hr className="h-[80%] border"></hr>
              <button className="cursor-pointer">VI</button>
            </div>
          </div>
        </div>
      </div>
      {/* hr */}
      <hr className="h-[2px] bg-[#9B8D6F] border-[white]"></hr>
      {/* menubar */}
      <div className="h-[100px] flex flex-row justify-between items-center mx-[70px]">
        <Link href={"/ring"}>Nhẫn</Link>
        <Link href={"/earring"}>Bông tai</Link>
        <Link href={"/necklace"}>Dây chuyền</Link>
        <Link href={"/bracelet"}>Vòng tay</Link>
        <div><a>Ưu đãi</a></div>

        {/* Search with debounce and dropdown results */}
        <div className="relative w-[350px]">
          <div className="w-full h-[50px] border rounded-full flex items-center px-5 bg-white">
            <input
              value={query}
              onChange={(e) => { setQuery(e.target.value); setIsOpen(true); }}
              onFocus={() => setIsOpen(true)}
              placeholder="Tìm kiếm sản phẩm"
              className="flex-1 outline-none placeholder-[#C0C0C0] bg-transparent"
            />
            <SearchIcon />
          </div>

          {isopen && debounced && (
            <div className="absolute z-50 mt-2 w-full bg-white border rounded-lg shadow-md max-h-[320px] overflow-auto">
              {status === "loading" && (
                <div className="p-3 text-sm text-gray-500">Đang tải dữ liệu…</div>
              )}
              {status !== "loading" && results.length === 0 && (
                <div className="p-3 text-sm text-gray-500">Không tìm thấy sản phẩm</div>
              )}
              {results.map((item) => (
                <div key={item.product_id} className="px-4 py-3 hover:bg-gray-50 cursor-pointer flex items-center justify-between">
                  <div className="text-sm text-gray-800">{item.name}</div>
                  <div className="text-xs text-gray-500">{formatPrice(item.price)}</div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
      <hr className="h-[2px] bg-[#9B8D6F] border-[white]"></hr>
    </div>
  );
};

export default Nav;
